@import '../../components/variables.module.scss';
@import '../../components/mixins.module.scss';
.cart {
   
    &__title {
        @include titles;
        grid-row: 1/2;
        grid-column: 1/11;
    }

    &__items {
        display: grid;
        grid-template-columns: repeat(11, min-content);
        justify-content: center;
        font-family: $fontDisplay;
        padding-top: 83px;
        @media(max-width:1180px) {
            grid-template-columns: repeat(8, min-content);
        }
        @media(max-width:880px) {
            grid-template-columns: repeat(6, min-content);
        }
        @media(max-width:725px) {
            display: flex;
            flex-direction: column;
            padding-top: 0;
        }
        
        &-titles {
            display: grid;
            grid-template-columns: repeat(11, 100px);
            align-items: center;
            justify-content: center;
            font-size: 16px;
            font-weight: 600;
            @media(max-width:1180px) {
                grid-template-columns: repeat(8, 100px);
            }
            @media(max-width:880px) {
                grid-template-columns: repeat(6, 100px);
            }
            @media(max-width:725px) {
                justify-content: start;
                padding: 10px;
                grid-template-columns: repeat(5, 55px);
            }
            
            &-product {
                grid-column: 3/8;
                @media(max-width:1180px){
                    grid-column: 3/6;
                }
                @media(max-width: 725px) {
                    grid-column: 1/3;
                }
            }
            &-price {
                grid-column: 8/10;
                @media(max-width:1180px){
                    grid-column: 6/7;
                }

                @media(max-width:725px){
                    grid-column: 5/6;
                }
            }

            &-subtotal {
                grid-column: 10/11;
                @media(max-width:1180px){
                    grid-column: 7/8;
                }
                @media(max-width:725px){
                    grid-column: 6/7;
                }
            }

        }
        &-list {
            grid-column: 1/11;
        }

        &-btngroup {
            grid-row:5/6;
            grid-column: 9/17;
            margin-bottom: 60px;
            @media(max-width:725px) {
                display: flex;
                flex-direction: column;
                text-align: center;
                padding:0 15px;
            }
            &-shop {
                font-size: 18px;
                font-weight: 500;
                padding: 9px;
                border: 2px solid #212F36;
                color: #212f36;
                margin-right: 20px;
                @media(max-width:725px) {
                    margin-right: 0;
                    margin-bottom: 15px;
                }
            }
            &-checkout {
                @include buttonOrder
            }
        }
        
    }
    &__item {
        display: grid;
        grid-template-columns: repeat(11, 100px);
        align-items: center;
        justify-content: center;
        font-size: 24px;
        margin-bottom: 52px;
        @media(max-width:1180px) {
            grid-template-columns: repeat(8, 100px);
        }
        @media(max-width:880px) {
            grid-template-columns: repeat(6, 100px);
            justify-content: start;
            padding: 10px;
        }
        @media(max-width:725px){
            grid-template-columns: repeat(5, 55px);
        }
        &-name {
            grid-column: 3/8;
            grid-row: 1/2;
            border-bottom: 1px solid $colorButton;
            padding-bottom: 10px;
            width: 90%;
            @media(max-width:1180px) {
                grid-column: 3/6;
            }
            @media(max-width:725px) {
                width: auto;
                grid-column: 1/3;
                grid-row: 1/2;
                border: none;
                font-size: 16px;
            }
            
        }
        &-price {
            grid-column: 8/10;
            @media(max-width:1180px) {
                grid-column: 6/7;
            }
            @media(max-width:725px){
                grid-column: 5/6;
                font-size: 16px;
            }
        }
        &-subtotal {
            grid-column: 10/11;
            @media(max-width:1180px) {
                grid-column: 7/8;
            }
            @media(max-width:725px){
                grid-column: 6/7;
                font-size: 16px;
            }
        }
        &-img {
            grid-column: 1/3;
            grid-row: 1/3;
            width: 169px;
            height: 169px;
            margin-right: 32px;
            @media(max-width:725px) {
                width: 55px;
                height: 55px;
                grid-row: 2/3;
            }
        }

        &-buttons {
            grid-column: 3/6;
            grid-row: 2/3;
            @media(max-width:770px) {
                grid-column: 3/5;
                grid-row: 1/2;
            }
            &-title {
                color: rgba($colorDisplay, 0.6);
                font-size: 16px;
                margin-right: 20px;
                @media(max-width:770px) {
                    display: none;
                }


            }

            &-rem,
            &-add {
                background-color: transparent;
                border:none;
                font-size: 32px;
                cursor: pointer;
                @media (max-width:725px) {
                    font-size: 16px;
                    font-weight: 700;
                }
            }
            &-rem {
                margin-right: 20px;
                @media(max-width:725px) {
                    margin-right: 0;
                }
            }
            &-add {
                margin-left: 20px;
                @media(max-width:725px) {
                    margin-left: 0;
                }
            }
            &-qty {
                font-size: 20px;
                padding: 16px 32px;
                border: 1px solid rgba($colorDisplay, 0.6);
                @media(max-width:725px) {
                    font-size: 16px;
                    padding: 4px 16px;
                }
            }
        }
    }


}
.totalprice {
    display: grid;
    grid-row: 3/4;
    grid-column: 9/10;
    grid-template-columns: repeat(2, min-content);
    border-top: 1px solid $colorButton;
    padding-top: 18px;
    grid-row-gap: 18px;
    font-size: 24px;
    margin-bottom: 140px;
    width: 290px;
    align-self: end;
    @media (max-width:725px) {
        font-size: 16px;
        width: auto;
    }
    &__subtotal-title,
    &__shipping-title,
    &__total-title {
        grid-column: 1/2;
        margin-right: 115px;
        @media(max-width:725px) {
            margin-right: 45px;
        }
    }
    &__subtotal-num,
    &__shipping-num,
    &__total-num {
        grid-column: 2/3;
    }

    &__subtotal-title,
    &__subtotal-num,
    &__shipping-title,
    &__shipping-num {
        color: rgba($colorDisplay, 0.6);
    }
}