@import "../../components/variables.module.scss";
@import "../../components/mixins.module.scss";

.favourites {
  &__title {
    @include titles;
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(3, min-content);
    justify-content: center;
    gap: 80px;
    margin-bottom: 50px;
    @media(max-width:1150px) {
      grid-template-columns: repeat(2, min-content);
    }
    @media(max-width:680px) {
      grid-template-columns: repeat(1, min-content);
    }
  }

  &__item {
    display: flex;
    flex-direction: column;

    img {
      width: 300px;
      height: 407px;
      object-fit: cover;
    }

    &-name {
      color: #212f36;
      font-size: 24px;
    }

    &-price {
      font-weight: 600;
      font-size: 36px;
      color: #000;
    }

    &-btn {
        outline: none;
        background-color: $colorButton;
        color: #fff;
        border: none;
        padding: 12px;
        font-weight: 700;
        font-family: $fontDisplay;
        font-size: 18px;
        cursor: pointer;
        &:hover {
            opacity: .8;
        }
    }
  }
}
