@import "../../components/variables.module.scss";
@import "../../components/mixins.module.scss";
.checkout {
  &__title {
    @include titles;
  }
  &__container {
    display: grid;
    grid-template-columns: repeat(2, min-content);
    grid-column-gap: 32px;
    justify-content: center;
    
    @media (max-width: 1435px) {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }
  
  &__container-title,
  &__form-title {
    font-size: 32px;
    font-weight: 500;
    margin-bottom: 32px;
    text-align: center;
    @media(max-width:725px) {
      font-size: 24px;
    }
  }

  &__review {
    grid-column: 1/2;
    margin-bottom: 20px;
    &-container {
      display: grid;
      padding: 52px;
      background-color: rgba(#e6f0f6, 0.6);
      border-radius: 0px 0px 0px 76px;
      grid-template-columns: repeat(8, 80px);
      @media (max-width: 1435px) {
        border-radius: 0;
      }
      @media (max-width: 765px) {
        grid-template-columns: repeat(5, 80px);
      }
      @media (max-width: 485px) {
        padding: 25px;
      }
    }
    &-titles {
      display: grid;
      grid-row: 1/2;
      grid-template-columns: repeat(8, 80px);
    }
    &-product {
      grid-column: 3/5;
      @media (max-width: 765px) {
        grid-column: 2/3;
      }
    }

    &-color {
      grid-column: 5/7;
      @media (max-width: 765px) {
        grid-column: 3/4;
      }
    }

    &-quantity {
      grid-column: 7/8;
      @media (max-width: 765px) {
        grid-column: 4/5;
      }
    }
    &-subtotal {
      grid-column: 8/9;
      @media (max-width: 765px) {
        grid-column: 5/6;
      }
    }
    &-product,
    &-color,
    &-quantity,
    &-subtotal {
      &::after {
        margin-top: 8px;
        content: "";
        display: block;
        height: 1px;
        background-color: #82b5d1;
      }
    }
  }

  &__items {
    grid-row: 2/3;
  }

  &__item {
    display: grid;
    grid-template-columns: repeat(8, 80px);
    align-items: center;
    margin-bottom: 36px;
    grid-row: 3/4;
    &-img {
      width: 48px;
      height: 48px;
      grid-column: 2/3;
      @media (max-width: 765px) {
        grid-column: 1/2;
      }
    }

    &-name {
      grid-column: 3/5;
      @media (max-width: 765px) {
        grid-column: 2/3;
      }
    }

    &-color {
      grid-column: 5/6;
      @media (max-width: 765px) {
        grid-column: 3/4;
      }
    }

    &-qty {
      grid-column: 7/8;
      justify-self: center;
      @media (max-width: 765px) {
        grid-column: 4/5;
      }
    }

    &-subtotal {
      grid-column: 8/9;
      justify-self: center;
      @media (max-width: 765px) {
        grid-column: 5/6;
      }
    }

    &-totaltitle {
      grid-column: 7/8;
      grid-row: 3/4;
      justify-self: center;
      font-weight: 700;
      @media (max-width: 765px) {
        grid-column: 4/5;
      }
    }

    &-totalprice {
      grid-column: 8/9;
      grid-row: 3/4;
      justify-self: center;
      @media (max-width: 765px) {
        grid-column: 5/6;
      }
    }

    &-totaltitle,
    &-totalprice {
      position: relative;
      padding-top: 5px;
      &::before {
        position: absolute;
        content: "";
        background-color: #82b5d1;
        height: 2px;
        width: 220%;
        margin-top: -5px;
      }
    }
  }
  &__form {
    grid-column: 2/3;
    display: grid;
    grid-template-columns: repeat(8, 80px);
    margin-bottom: 50px;
    @media(max-width:725px) {
      grid-template-columns: repeat(8, 45px);
    }
    & input,
    select,
    &-subtitle {
      margin-bottom: 24px;
    }

    & input[type="text"],
    input[type="tel"],
    select {
      height: 40px;
    }
    &-title {
      grid-column: 1/8;
    }

    &-subtitle,
    &-check--label,
    &-check--input,
    &-notes--label,
    &-notes--input {
      grid-column: 1/9;
    }

    &-name--input,
    &-name--label,
    &-tel--input,
    &-tel--label,
    &-paytitle,
    &-valid--input,
    &-valid--label {
      grid-column: 1/4;
      @media (max-width: 725) {
        grid-column: 1/8;
      }
    }
    &-street--input,
    &-street--label,
    &-town--input,
    &-town--label {
      grid-column: 1/5;
      @media (max-width: 725) {
        grid-column: 1/8;
      }
    }
    &-email--label,
    &-email--input,
    &-cvv--label,
    &-cvv--input {
      grid-column: 5/9;
      @media (max-width: 725) {
        grid-column: 1/8;
      }
    }
    &-cardholder--label,
    &-cardnumber--label,
    &-cardholder--input,
    &-cardnumber--input {
      grid-column: 1/6;
      @media (max-width: 725) {
        grid-column: 1/8;
      }
    }

    &-apt--label,
    &-zip--label,
    &-apt--input,
    &-zip--input,
    &-submit {
      grid-column: 6/9;
      @media (max-width: 725) {
        grid-column: 1/8;
      }
    }
    &-name--label,
    &-email--label {
      grid-row: 3/4;
    }

    &-name--input,
    &-email--input {
      grid-row: 4/5;
    }

    &-street--label,
    &-apt--label {
      grid-row: 5/6;
    }
    &-street--input,
    &-apt--input {
      grid-row: 6/7;
    }
    &-town--label,
    &-zip--label {
      grid-row: 7/8;
    }
    &-town--input,
    &-zip--input {
      grid-row: 8/9;
    }

    &-tel--label {
      grid-row: 11/12;
    }
    &-tel--input {
      grid-row: 12/13;
    }
    &-check--input,
    &-check--label {
      grid-row: 13/14;
    }
    &-notes--label {
      grid-row: 15/16;
    }
    &-notes--input {
      grid-row: 14/15;
    }
    &-paytitle {
      grid-row: 16/17;
    }
    &-payment--input-1,
    &-payment--label-1 {
      grid-row: 17/18;
    }
    &-payment--input-2,
    &-payment--label-2 {
      grid-row: 18/19;
    }
    &-cardholder--label {
      grid-row: 19/20;
    }
    &-cardholder--input {
      grid-row: 20/21;
    }
    &-cardnumber--label {
      grid-row: 21/22;
    }
    &-cardnumber--input {
      grid-row: 22/23;
    }
    &-valid--label,
    &-cvv--label {
      grid-row: 23/24;
    }
    &-valid--input,
    &-cvv--input {
      grid-row: 24/25;
    }

    &-submit {
      grid-row: 25/26;
      @include buttonOrder;
      cursor: pointer;
      border: none;
      font-weight: 700;
      font-family: $fontDisplay;
    }

    &-check--label {
      margin-left: 30px;
    }

    &-paytitle,
    &-subtitle {
      font-size: 24px;

      grid-column: 1/9;
      margin-bottom: 32px;
    }
    &-paytitle {
      padding-top: 45px;
    }
   
  }
}
