@import "../../components/variables.module.scss";
@import "../../components/mixins.module.scss";
.about {
  &__hero {
    @include heroImage("https://i.imgur.com/4WQrGTe.jpg");
    @media (max-width: 1025px) {
      box-shadow: inset 0 0 0 1000px rgb(0 0 0 / 40%);
    }
  }

  &__post {
    display: grid;
    justify-content: center;
    align-items: center;
    grid-template-columns: repeat(12, 80px);
    @media (max-width: 900px) {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    &-img {
      width: 400px;
      height: 350px;
      object-fit: cover;
      grid-column: 1/6;
      grid-row: 1/3;
      @media (max-width: 900px) {
        width: 250px;
        height: 180px;
      }
    }

    &-title {
      grid-column: 7/12;
      grid-row: 1/2;
      font-weight: 700;
      font-size: 28px;
      white-space: nowrap;
      align-self: end;
      @media (max-width: 900px) {
        align-self: center;
        font-size: 20px;
      }
    }

    &-text {
      grid-column: 7/12;
      grid-row: 2/3;
      font-size: 18px;
      align-self: start;
      @media (max-width: 900px) {
        font-size: 16px;
      }
    }
  }

  &__quote {
    font-size: 48px;
    font-weight: 700;
    text-align: center;
    padding: 5% 15%;
    @media (max-width: 900px) {
      font-size: 24px;
      padding: 0;
    }
  }
}
